import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { RegisterPage } from "../register/register";
import { HomePage } from "../home/home";

import { Http, Headers } from '@angular/http';
import { ApiServiceProvider } from "../../providers/apiservice";
import { TabsPage } from "../tabs/tabs";
 
/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

 name: string;
  
  email: string;
 
  number: number;
 uid:any;
 username:number;
 password:any;
 token:any;
  constructor(public nav: NavController,
   public http: Http,private apiService:ApiServiceProvider) {
   this.token=window.localStorage.getItem('token');
      this.username=JSON.parse(window.localStorage.getItem('mobile_no'));
      console.log('mob no',this.username);
  }
 
  login(){
  let user = {
       
        username:this.username,
       password: this.password
      };
      console.log(user);
  this.apiService.login123(user)
  .subscribe((res) => {
    window.localStorage.setItem('login_token',JSON.stringify(res));
console.log(res.json());
this.nav.push(TabsPage);
  },
  (err)=>{
    console.log(err);
  });
  
  }
 






}



